*, *::before, *::after {box-sizing: border-box;}
ul[class],ol[class] {padding: 0;}
body,h1,h2,h3,h4,p,ul[class],ol[class],li,figure,figcaption, blockquote,dl,dd {margin: 0; padding: 0}
body {min-height: 100vh;scroll-behavior: smooth;text-rendering: optimizeSpeed;line-height: 1.5;}
ul[class], ol[class] {list-style: none;}  a:not([class]) {text-decoration-skip-ink: auto;}
img {max-width: 100%;display: block;}
article > * + * {margin-top: 1em;}
input,button,textarea,select {font: inherit;}
a{text-decoration: none}

$pink:#a82682;

body{
  height: 100%;
  width: 100%;
  font-family: "Helvetica",sans-serif;
}
.wrapper{
  overflow: hidden;
  height: 100%;
}
.container{
  max-width: 1243px;
  margin: 0 auto;
}
.header{
  background-color: $pink;
  padding: 11px;
  &__row{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__left{
    display: flex;
  }
  &__right{
    display: flex;
    align-items: center;
  }
  &__item{
    text-transform: uppercase;
    color: white;
    margin-right: 40px;
    font-size: 18px;
    font-weight: 600;
  }

  &__shop-cart{
    margin-right: 15px;
  }
  &__search{
    margin-right: 15px;
  }
  &__phone{
    font-size: 18px;
    color: white;
  }
  &__login{
    color: white;
    margin: 11px 0 11px 0;
  }
}
.header__img:first-child{
  margin-right: 24px;
}
.header__item:last-child{
  margin-right: 0;
}


.nav{
  padding: 30px 0 25px 0;
  box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
  &__row{
    display: flex;
    align-items: center;
  }
  &_banner{
    flex: 1 1 auto;
  }
  &__list{
    display: flex;
  }
  &__item {
    color: #2b2a29;
    text-transform: uppercase;
    font-family: HelveticaNeue,sans-serif;
    font-size: 14px;
    line-height: 1.21;
    margin: 0 48px 0 0;
    cursor: pointer;
  }
  &__arrow{
    display: inline-block;
    margin-bottom: 3px;
    cursor: pointer;
  }
  &__eye{
  }
}
.promo{
  padding: 164px 0 356px 0;
  &__row{
  }
  &__title{
    text-transform: uppercase;
    color: #2b2a29;
    font-size: 54px;
    margin-top: 40px ;
    margin-bottom: 14px;
  }
  &__title_color{
    color: #a82682;
  }
  &__text{
    font-size: 16px;
    color: #2b2a29;
    margin: 0 0 57px 0;
    line-height: 1.5;
    font-family: 'Helvetica',sans-serif;
  }
  &__line{
    display: inline-block;
    margin-right: 7px;
    margin-left: 7px;
  }

  &__number{
    display: inline-block;
    position: relative;
    top:3px;
    font-size: 14px;
    font-weight: bold;
  }
  &__number:last-child{
    opacity: 0.3;
  }
  &__button{
    padding: 14px 43px;
    background-color: #a82682;
    color: white;
    border: 1px solid #a82682;
  }
}
